aside.rnb {
  position: fixed;
  top: 0;
  left: 100%;
  height: 100%;
  width: 235px;
  min-height: 100%;
  background: #f3848c;
  transition: all 0.3s ease-in-out;
  z-index: 99999;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15); }
  aside.rnb > ul {
    height: 100%;
    overflow-y: auto; }
  aside.rnb:hover {
    left: calc(100% - 235px); }
  aside.rnb .handle {
    position: absolute;
    top: 100px;
    right: 100%;
    display: block;
    padding: 5px;
    color: #fff;
    border-radius: 5px 0 0 5px;
    background: #f3848c;
    box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15); }
  aside.rnb li {
    background: transparent;
    border-bottom: 1px solid #facacd; }
    aside.rnb li a {
      display: block;
      padding: 15px 10px 15px 15px;
      color: #fff;
      transition: all 0.3s ease-in-out; }
      aside.rnb li a i {
        display: inline-block;
        height: 23px;
        line-height: 23px;
        margin-right: 10px;
        font-size: 16px;
        vertical-align: top; }
        aside.rnb li a i.downbullet {
          float: right;
          font-size: 16px !important;
          margin-right: 0;
          transition: all 0.3s ease-in-out; }
      aside.rnb li a span {
        display: inline-block;
